import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {useNavigate, Link} from "react-router-dom";
import Data from "../data";

export default function Cat() {
  const data = [
    "TECNOLOGY",
    "ENVIRONMENT",
    "POLITICS",
    "LIFESTYLE",
    "BUSINESS",
    "HEALTH",
    "EDUCATION",
    "FASHION",
    "INTERNATIONAL",
  ];

  // console.log(Data);

  const navigate = useNavigate();

  function handleClick(e) {
    navigate("/category", {state: {catname: e}});
  }

  function handletop(e){
    navigate("/article",{state:{Id:e}})
  }

  const showCat = () => {
    return data.map((name, index) => {
      const Name = name;

      function Topthree() {
        return Data.map((topa, index) => {
          if (Name === topa.cat && topa.Id <= 27) {
            return (
              <div onClick={()=>handletop(topa.Id)} className="cat-art">
                <div className="cat-img-con">
                  <img
                    className="cat-img"
                    src={topa.Img1}
                  ></img>
                </div>
                <div className="cat-text">
                  {topa.Title}
                </div>
              </div>
            );
          }
        });
      }

      return (
        <Col className="cat-org">
          <div
            style={{
              marginTop: "50px",
              marginBottom: "5px",
              fontSize: 18,
              fontWeight: "600",
              letterSpacing: 2,
              color: "#8b8b8b",
            }}
          >
            {Name}
          </div>
          <div class="tit-ul2" style={{marginBottom: "30px"}}></div>
          {Topthree()}
          <div onClick={() => handleClick(Name)} className="more-btn">
            MORE
          </div>
        </Col>
      );
    });
  };

  return (
    <Container className="cat-con">
      <Row className="cat-box" sm={1} xs={1} md={2} lg={3}>
        {showCat()}
      </Row>
    </Container>
  );
}
