import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import Offcanvas from "react-bootstrap/Offcanvas";

export default function NavbarResponsive() {
  return (
    <>
      {["md"].map((expand) => (
        <Navbar
          fixed="top"
          key={expand}
          expand={expand}
          style={{
            marginLeft: "10px",
            marginRight: "10px",
            backgroundColor: "#daded9",
            borderTop:"solid",
            borderTopWidth:"10px",
            borderTopColor:"white"
          }}
          className="bg-body-tertiary"
        >
          <Container
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
            fluid
          >
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
              }}
            >
              <Navbar.Brand style={{marginRight: "6px"}} href="#">
                <div id="foot-logo">
                  <svg
                    class="small-svg"
                    version="1.0"
                    xmlns="http://www.w3.org/2000/svg"
                    width="297.000000pt"
                    height="296.000000pt"
                    viewBox="0 0 297.000000 296.000000"
                    preserveAspectRatio="xMidYMid meet"
                  >
                    <g transform="translate(0.000000,296.000000) scale(0.100000,-0.100000)">
                      <path
                        class="svg-path"
                        d="M192 2780 l-22 -9 2 -718 3 -718 73 -3 72 -3 0 656 0 655 615 0 615
                            0 0 -845 0 -845 70 0 70 0 0 915 0 915 -702 0 c-387 0 -719 2 -738 5 -19 3
                            -45 0 -58 -5z"
                      />
                      <path
                        class="svg-path"
                        d="M1964 2766 c-3 -7 -4 -38 -2 -67 l3 -54 295 -1 c162 0 325 -1 363 -2
                            l67 -2 -2 -1182 -3 -1183 -1182 -3 -1183 -2 -1 142 c-1 79 -1 172 -1 208 0 36
                            1 125 1 198 l1 132 420 0 c394 0 422 1 472 20 53 19 116 74 150 130 27 45 41
                            134 29 191 -22 101 -76 170 -166 211 -46 21 -67 23 -295 28 -278 6 -282 7
                            -320 86 -40 82 4 178 95 203 26 7 159 11 368 11 l328 0 -3 68 -3 67 -360 0
                            c-345 0 -362 -1 -406 -21 -66 -31 -115 -78 -146 -142 -22 -46 -27 -69 -27
                            -128 1 -113 56 -203 157 -256 39 -21 57 -23 294 -28 234 -5 253 -6 279 -26 71
                            -53 90 -121 52 -193 -43 -83 -26 -81 -575 -81 l-483 0 0 -480 0 -480 1323 2
                            1322 3 0 1320 0 1320 -428 3 c-356 2 -428 0 -433 -12z"
                      />
                      <path
                        class="svg-path"
                        fill="#fd3419"
                        d="M2102 1663 l-261 -308 -1 -110 c0 -94 2 -107 14 -95 8 8 47 53 86
                          100 40 47 76 86 80 88 5 2 82 -85 171 -192 l164 -195 93 0 c50 -1 92 1 92 3 0
                          3 -95 116 -210 252 -116 136 -209 251 -208 255 2 4 95 116 208 249 113 132
                          207 245 208 250 2 6 -33 10 -85 10 l-90 0 -261 -307z"
                      />
                    </g>
                  </svg>
                </div>
              </Navbar.Brand>
              <Navbar.Brand
                style={{color: "#4b504a", fontWeight: 700, marginLeft: "0px"}}
                href="#"
              >
                SpotK
              </Navbar.Brand>
            </div>
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Navbar.Brand
                style={{
                  margin: 0,
                  padding: "0px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <button className="searchbutton">
                  <svg
                    className="searchsvg"
                    xmlns="http://www.w3.org/2000/svg"
                    width="48"
                    height="48"
                    viewBox="0 0 48 48"
                  >
                    <mask id="ipTSearch0">
                      <g
                        fill="#dadfd9"
                        stroke="#fff"
                        stroke-linejoin="round"
                        stroke-width="4"
                      >
                        <path
                          fill="#555"
                          d="M21 38c9.389 0 17-7.611 17-17S30.389 4 21 4S4 11.611 4 21s7.611 17 17 17Z"
                        />
                        <path
                          stroke-linecap="round"
                          d="M26.657 14.343A7.975 7.975 0 0 0 21 12a7.975 7.975 0 0 0-5.657 2.343m17.879 18.879l8.485 8.485"
                        />
                      </g>
                    </mask>
                    <path
                      fill="#505050"
                      d="M0 0h48v48H0z"
                      mask="url(#ipTSearch0)"
                    />
                  </svg>
                </button>
              </Navbar.Brand>
              <Navbar.Toggle
                aria-controls={`offcanvasNavbar-expand-${expand}`}
              />
            </div>

            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-${expand}`}
              aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
              placement="end"
            >
              <Offcanvas.Header closeButton>
                <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
                  Offcanvas
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav
                  style={{margin: 0}}
                  className="justify-content-end flex-grow-1 pe-3"
                >
                  <Nav.Link href="#action1">FEATURED</Nav.Link>
                  <Nav.Link href="#action2">POPULAR</Nav.Link>
                  <Nav.Link href="#action1">CONTACT</Nav.Link>
                  <NavDropdown
                    style={{fontWeight: 900,color:"#142A0F"}}
                    title="CATEGORIES"
                    id={`offcanvasNavbarDropdown-expand-${expand}`}
                  >
                    <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                    <NavDropdown.Item href="#action4">
                      Another action
                    </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action5">
                      Something else here
                    </NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
      ))}
    </>
  );
}
