import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {useLocation, useNavigate} from "react-router-dom";
import Data from "../data";
export default function Category() {


  const {state} = useLocation();
  const {catname} = state;
  console.log(catname);
  const navigate = useNavigate();

  function handleClick(e){
    navigate("/article",{state:{Id:e}})
  }


  function showAll() {
    return Data.map((name, index) => {
      if (name.cat == catname) {
        const id = name.Id;
        const img1 =  name.Img1;
        return (
          <Col className="cat-hov">
          <div onClick={()=>handleClick(id)} className="cat-art">
            <div className="cat-img-con">
              <img
                className="cat-img"
                src={img1}
              ></img>
            </div>
            <div className="cat-text">{name.Title}</div>
          </div>
        </Col>
        );
      }
    });
  }

  

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        marginTop: "70px",
      }}
    >
      <div
        style={{
          marginTop: "50px",
          marginBottom: "5px",
          fontSize: 18,
          fontWeight: "600",
          letterSpacing: 2,
          color: "#8b8b8b",
        }}
      >
        {catname}
      </div>
      <div className="tit-ul2"></div>
      <Container style={{marginTop: "50px"}}>
            <Row lg={3} sm={1} xs={1} md={2}>
              {showAll()}
            </Row>
          </Container>
    </div>
  );
}
