import React, { useState } from "react";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import ViewInArIcon from "@mui/icons-material/ViewInAr";
import GitHubIcon from "@mui/icons-material/GitHub";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import Zoom from '@mui/material/Zoom';
import MailOutlineIcon from '@mui/icons-material/MailOutline';




export default function Icon() {
  const [onChecked, setonChecked] = useState(true);
  if (document.body.scrollTop > 50 ) {
    setonChecked(false);
  }
  
  const [icon1,seticon1]=useState(false);
  const [icon2,seticon2]=useState(false);
  const [icon3,seticon3]=useState(false);
  const [icon4,seticon4]=useState(false);
  const [icon5,seticon5]=useState(false);


  return (
    <>
      
      <Zoom onClick={()=>window.open('https://www.linkedin.com/in/sai-sudha-kiran-sobila-248453199', '_blank')} in={onChecked}  onMouseEnter={()=>seticon1(true)} onMouseLeave={()=>seticon1(false)}
      style={{ transitionDelay: onChecked ? "460ms" : "0ms", cursor:"pointer",height:"50px",width:"35px" }}
      >
        <LinkedInIcon sx={{color: icon1 ? "skyblue":"black",}} className="soc-icons-div-div" />
      </Zoom>
      <Zoom onClick={()=>window.open('https://codesandbox.io/u/saisobila', '_blank')} onMouseEnter={()=>seticon2(true)} onMouseLeave={()=>seticon2(false)}
        in={onChecked}
        style={{ transitionDelay: onChecked ? "400ms" : "0ms",cursor:"pointer",height:"50px",width:"35px" }}
      >
        <ViewInArIcon sx={{color: icon2 ? "pink":"black",}} className="soc-icons-div-div"  />
      </Zoom>
      <Zoom onClick={()=>window.open('https://github.com/saisobila', '_blank')} onMouseEnter={()=>seticon3(true)} onMouseLeave={()=>seticon3(false)}
        in={onChecked}
        style={{ transitionDelay: onChecked ? "340ms" : "0ms",cursor:"pointer",height:"50px",width:"35px" }}
      >
        <GitHubIcon sx={{color: icon3 ? "#f5945f":"black",}} className="soc-icons-div-div" />
      </Zoom>
      <Zoom onClick={()=>window.open('mailto:saisudhakiransobila@gmail.com', '_blank')} onMouseEnter={()=>seticon4(true)} onMouseLeave={()=>seticon4(false)}
        in={onChecked}
        style={{ transitionDelay: onChecked ? "290ms" : "0ms",cursor:"pointer",height:"50px",width:"35px" }}
      >
        <MailOutlineIcon sx={{color: icon4 ? "#B23121":"black",}} className="soc-icons-div-div" />
      </Zoom>
      <Zoom onClick={()=>window.open('https://wa.me/+918985919149', '_blank')} onMouseEnter={()=>seticon5(true)} onMouseLeave={()=>seticon5(false)}
        in={onChecked}
        style={{ transitionDelay: onChecked ? "230ms" : "0ms",cursor:"pointer",height:"50px",width:"35px" }}
      >
        <WhatsAppIcon sx={{color: icon5 ? "lightgreen":"black",}} className="soc-icons-div-div" />
      </Zoom>

     
    </>
  );
}
