import React from "react";
import Data from "../data"

export default function dqoute() {
  return (
    <div
      style={{display: "flex", flexDirection: "column", alignItems: "center"}}
    >
      <div style={{marginTop: 48,color:"#8b8b8b", fontSize: 18, fontWeight: 600,letterSpacing:1.5}}>
        DAY QUOTE
      </div>
      <div class="tit-ul2"></div>
      <div style={{display:"flex",flexDirection:"row",marginTop:"40px",alignItems:"center",justifyContent:"center",lineHeight:1.6,textAlign:"center"}}>
        <div className="q-text"><img alt=",," height={20} style={{marginTop:"-20px"}} src={"./images/quote1.png"}></img>{Data[0].dq}<img alt=",," height={20} style={{marginTop:"-20px"}} src={"./images/quote2.png"}></img></div>
      </div>
    </div>
  );
}
  