import React from "react";
import Data from "../data.js";
import { useLocation } from "react-router-dom";



export default function Article() {

  const {state} = useLocation();
  const {Id} = state;
  console.log(Id);


  function showArt(){
    return Data.map((newData)=>{
      if( newData.Id === Id){

      
      return (
        <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <div
          className="art-con"
          style={{
            marginBottom: "30px",
            marginTop: "70px",
          }}
        >
          <img
            className="art-img"
            src={newData.Img1}
          ></img>
          <div className="art-img-bg"></div>
          <div className="tit-box">
            <div className="art-tit">
              {newData.Title}
              <div
                className="tit-ul2"
                style={{
                  bottom: "-30px",
                  position: "absolute",
                  backgroundColor: "white",
                }}
              ></div>
            </div>
          </div>
        </div>
        <div className="art-con2">
          <div className="para">{newData.Para1}</div>
          <div className="art-con">
            <img
              className="art-img"
              src={newData.Img2}
            ></img>
          </div>
          <div className="para">{newData.para2}</div>
          <div className="art-con">
            <img
              className="art-img"
              src={newData.Img3}
            ></img>
          </div>
          <div className="para">{newData.para3}</div>
        </div>
      </div>
      )}
    })
  
  };

  // console.log(Data.environment.Id);

  return (
  <div>{showArt()}</div>
  );
}
