import React from 'react'

export default function Sub() {


  return (
    <div className='sub-con' >
      <div style={{
          fontSize: 18,
          fontWeight: 600,
          color: "#8b8b8b",
          letterSpacing: 2,
          marginBottom:10,
        }}>SUBSCRIBE</div>
      <div >sign-up to our NewsLetter</div>
      <input className='email-input' type='email' placeholder='Email' ></input>
      <div className="more-btn" style={{width:"110px",textAlign:"center"}}>SUBMIT</div>

    </div>
  )
}
